import React, {useState, useEffect} from "react";
import s from "./KeyBoard.module.css"
import Button from "../Button/Button";

const TONES = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'];
const MAP_KEYS= {
    "C4" : "A",
    "D4" : "S",
    "E4" : "D",
    "F4" : "F",
    "G4" : "G",
    "A4" : "H",
    "B4" : "J",
    "C5" : "K",
    "D5" : "L",
    "E5" : "Semicolon",
    "F5" : "Quote",
    "G5" : "Z",
    "A5" : "X",
    "B5" : "C",
    "C#4" : "W",
    "D#4" : "E",
    "F#4" : "T",
    "G#4" : "Y",
    "A#4" : "U",
    "C#5" : "O",
    "D#5" : "P",
    "F#5" : ",",
    "G#5" : ".",
    "A#5" : "/"
}

function Keyboard({audioPlayer,  downloadNots, img}) {
    const [octavsStart, setOctavsStart] = useState(3);
    const [octavsEnd, setOctavsEnd] = useState(5);

    function rerender(){
        setOctavsStart(refStart.current.value);
        setOctavsEnd(refEnds.current.value);
    }
    const OCTAVE = []
    let i = octavsStart - 1
    while (++i <= octavsEnd) {
        TONES.forEach(el2 => {
            OCTAVE.push(el2 + i)
        })
    }
    const keys = OCTAVE.map(el => <Button note={el} audioPlayer={audioPlayer} MAP_KEYS={MAP_KEYS}/>)


    let refStart = React.createRef();
    let refEnds = React.createRef();
    return (
        <div className={s.items}>
            <div>
                Левая октава:
                <select className={s.sel} ref={refStart} onChange={rerender}>
                    <option>1</option>
                    <option>2</option>
                    <option selected>3</option>
                    <option>4</option>
                    <option>5</option>
                    <option>6</option>
                </select>
                Правая октава:
                <select className={s.sel} ref={refEnds} onChange={rerender}>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option selected>5</option>
                    <option>6</option>
                </select>
                <div className={s.nots}>
                    Ноты:
                    <input ref={img} type="text"/>
                    <button onClick={downloadNots}>Загрузить ноты</button>
                </div>
            </div>
            <div className={s.keyBoard}>
                {keys}
            </div>
        </div>
    )
}

export default Keyboard;