import React from "react";
import s from './Button.module.css';

function Button({note, audioPlayer, MAP_KEYS}) {

    let letter;
    letter = MAP_KEYS[note];
    let name = letter
    if (name && name.length > 1) {
        name = ";"
    }
    if (letter && letter.length === 1) {
        letter = "Key" + letter;
    }
    document.addEventListener('keypress', function (event) {
        if (letter && event.code === letter) {
            handleClick()
        }
    });
    const ref = React.createRef()
    function handleClick () {
        audioPlayer.playNote(note);
    }
    const handleClick2 = () => {
        handleClick()
    }
    if (note.length == 2) {
        return (
            <button className={s.butReg} ref={ref} onClick={handleClick2}>{name}</button>
        )
    } else {
        return (
            <button className={s.butDiez} ref={ref} onClick={handleClick2}>{name}</button>
        )
    }
}


export default Button