import AudioPlayer from "./AudioContext/AudioPlayer";
import React, {useEffect, useState} from "react";
import Button from "./Button/Button";
import s from "./StartApp.module.css";
import Keyboard from "./KeyBoard/KeyBoard";
const audioPlayer = AudioPlayer();

function StartApp() {

    const [refNots, setRefNots] = useState(null);
    useEffect(() => {
        audioPlayer.setInstrument("acoustic_grand_piano");
    }, []);
    const img = React.createRef();

    function downloadNots() {
        setRefNots(img.current.value);
    }

    return (
        <div className={s.mainThem}>
            {refNots ? <div className={s.top}><img src={refNots} alt=""/></div> : <></>}
            <div className={s.keyBoard}><Keyboard audioPlayer={audioPlayer} downloadNots={downloadNots} img={img}/></div>
        </div>
    );
}

export default StartApp
